<template>
  <div class="mobile-container">
    <Header/>
    <div class="m-tips">
      {{ $t('lang.token_detail_title') }}
    </div>
    <div class="m-content">
      <div class="m-head">
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table1') }}
          </div>
          <p>
            {{ transData.name }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table2') }}
          </div>
          <p>
            <router-link tag="a" :to="{path:'/mtx',query:{txid: transData.txhash }}">{{ transData.txhash }}
            </router-link>
          </p>
          <img src="@/assets/img/copy.png" alt="" @click="copy(transData.txhash)">
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table3') }}
          </div>
          <p style="color:rgba(226,138,4,1);">{{ transData.status }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table4') }}
          </div>
          <p>
            {{ transData.block }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table5') }}
          </div>
          <p>
            {{ filterDate(transData.timestamp) }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table6') }}
          </div>
          <p>
            <router-link tag="a" :to="{path:'/maddress',query:{address: transData.from_gkc }}">{{ transData.from_gkc }}
            </router-link>
          </p>
          <img src="@/assets/img/copy.png" alt="" @click="copy(transData.from_gkc)">
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table7') }}
          </div>
          <p>
            <router-link tag="a" :to="{path:'/maddress',query:{address: transData.to_gkc }}">{{ transData.to_gkc }}
            </router-link>
          </p>
          <img src="@/assets/img/copy.png" alt="" @click="copy(transData.to_gkc)">
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table8') }}
          </div>
          <p>
            {{ transData.value }} {{ transData.symbol }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table9') }}
          </div>
          <p>
            {{ transData.amount }} {{ transData.symbol }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table10') }}
          </div>
          <p>
            {{ transData.fee }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table11') }}
          </div>
          <p>
            {{ transData.gaslimit }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel">
            {{ $t('lang.token_detail_table12') }}
          </div>
          <p>
            {{ transData.gasprice }}
          </p>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/MHeader'
  import Footer from '@/components/MFooter'
  import pageSize from '@/components/MPage'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        transData: [],
        txid: ""
      }
    },
    created() {
      this.txid = this.$route.query.txid;
    },
    computed: {},
    mounted() {
      this.getTokenInfoByHash()
    },
    methods: {
      getTokenInfoByHash() {
        this.$http.post("api/contract/detail", {txid: this.txid}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.data
          }
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      copy(data){
        let url = data;
        let oInput = document.createElement('input');
        oInput.value = url;
        document.body.appendChild(oInput);
        oInput.select();
        document.execCommand("Copy");
        this.$message.success({
          message: 'Replication success!',
          type: 'success'
        });
        oInput.remove()
      },
    }
  }


</script>


<style scoped>
  .m-content {
    padding: 1rem;
    background: #E6E9EE;
  }

  .m-head {
    background: #ffffff;
  }

  .m-head .m-item {
    text-align: center;
    border-bottom: 1px solid #F0F0F0;
    padding: 1rem 0;
    display: flex;
    justify-content: left;
    padding-left: 16px;
  }

  .m-item p {
    font-size:14px;
    font-weight:400;
    color:rgba(32,38,64,1);
    opacity:1;
    width: 230px;
    text-align: left;
  }
  .m-item p a {
    word-break: break-word;
    vertical-align: middle;
    display: inline-block;
  }
  .m-item img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
    margin-left: 12px;
    cursor: pointer;
  }

  .item-lebel {
    font-size:14px;
    font-weight:400;
    color:rgba(32,38,64,1);
    opacity:1;
    width: 85px;
    text-align: left;

  }
  .m-tips {
    font-size:12px;
    color:rgba(0,0,0,1);
    opacity:1;
    padding: .7rem;
    text-align: left;
    margin-top: 1rem;
  }
</style>



